"use client";

import React, { useState } from 'react';

export default function RecommendationPage() {
    const [genre, setGenre] = useState('');
    const [rating, setRating] = useState('');
    const [recommendation, setRecommendation] = useState(null);

    const genres = ['Action', 'Comedy', 'Drama', 'Horror', 'Romance', 'Sci-Fi', 'Thriller'];

    const handleGetRecommendation = async () => {
        // Mocked data
        const mockData = [
            { title: "Inception", genre: "Sci-Fi", rating: 8.8 },
            { title: "The Notebook", genre: "Romance", rating: 7.9 },
            { title: "Get Out", genre: "Horror", rating: 7.7 },
            { title: "Avengers", genre: "Action", rating: 8.0 },
        ];

        // Filter mock data
        const filteredData = mockData.filter((movie) => {
            return (
                (!genre || movie.genre === genre) &&
                (!rating || movie.rating >= parseFloat(rating))
            );
        });

        // Pick a random recommendation
        const randomIndex = Math.floor(Math.random() * filteredData.length);
        setRecommendation(filteredData[randomIndex] || null);
    };

    return (
        <div>
            <h1>Get a Recommendation</h1>

            <div>
                <label>
                    Genre:
                    <select value={genre} onChange={(e) => setGenre(e.target.value)}>
                        <option value="">All</option>
                        {genres.map((g, index) => (
                            <option key={index} value={g}>
                                {g}
                            </option>
                        ))}
                    </select>
                </label>
                <label>
                    Minimum Rating:
                    <input
                        type="number"
                        value={rating}
                        onChange={(e) => setRating(e.target.value)}
                        min="0"
                        max="10"
                    />
                </label>
                <button onClick={handleGetRecommendation}>Get Recommendation</button>
            </div>

            <div>
                <h2>Recommended Movie/Series:</h2>
                {recommendation ? (
                    <div>
                        <p><strong>Title:</strong> {recommendation.title}</p>
                        <p><strong>Genre:</strong> {recommendation.genre}</p>
                        <p><strong>Rating:</strong> {recommendation.rating}</p>
                    </div>
                ) : (
                    <p>No recommendation available. Adjust your filters and try again.</p>
                )}
            </div>
        </div>
    );
}
